body {
  margin: 0;
  padding: 0;

  font-family: "Montserrat",  sans-serif;
}

img {
  width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  cursor: pointer;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

input {
  font-family: inherit; 
  font-size: inherit; 
  line-height: inherit; 
  margin: 0;
  cursor: pointer;
}


